'use client'

import { useState, useEffect } from 'react'
import { CustomFieldDefinition } from '@/components/custom-fields/CustomFieldInput'

export default function CustomFieldsSettingsPage() {
  const subAccountId = 'sub-001' // TODO: Get from auth/context
  
  const [fields, setFields] = useState<CustomFieldDefinition[]>([])
  const [loading, setLoading] = useState(true)
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [editingField, setEditingField] = useState<CustomFieldDefinition | null>(null)
  const [error, setError] = useState('')

  // Fetch custom fields
  useEffect(() => {
    fetchFields()
  }, [])

  const fetchFields = async () => {
    try {
      setLoading(true)
      const response = await fetch(`/api/v1/custom-fields?subAccountId=${subAccountId}`)
      const data = await response.json()

      if (data.success) {
        setFields(data.data || [])
      } else {
        setError(data.error || 'Failed to load custom fields')
      }
    } catch (err: any) {
      console.error('Failed to fetch custom fields:', err)
      setError('Failed to load custom fields')
    } finally {
      setLoading(false)
    }
  }

  // Delete field
  const handleDelete = async (fieldId: string) => {
    if (!confirm('Are you sure you want to delete this custom field? This action cannot be undone.')) {
      return
    }

    try {
      const response = await fetch(
        `/api/v1/custom-fields/${fieldId}?subAccountId=${subAccountId}`,
        { method: 'DELETE' }
      )
      const data = await response.json()

      if (data.success) {
        fetchFields() // Refresh list
      } else {
        alert(data.error || 'Failed to delete field')
      }
    } catch (err: any) {
      console.error('Failed to delete field:', err)
      alert('Failed to delete field')
    }
  }

  // Group fields by entity type
  const fieldsByEntity = fields.reduce((acc, field) => {
    if (!acc[field.entityType]) {
      acc[field.entityType] = []
    }
    acc[field.entityType].push(field)
    return acc
  }, {} as Record<string, CustomFieldDefinition[]>)

  return (
    <div className="min-h-screen bg-gray-50 p-4 sm:p-6 lg:p-8">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Custom Fields</h1>
          <p className="mt-2 text-gray-600">
            Define custom fields to capture additional information for contacts, deals, and more.
          </p>
        </div>

        {/* Actions */}
        <div className="mb-6 flex gap-3">
          <button
            onClick={() => setShowCreateModal(true)}
            className="px-6 py-3 bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-lg font-medium hover:from-purple-700 hover:to-pink-700 transition-all flex items-center gap-2"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
            Add Custom Field
          </button>
        </div>

        {/* Error Message */}
        {error && (
          <div className="mb-6 bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded-lg">
            {error}
          </div>
        )}

        {/* Loading State */}
        {loading ? (
          <div className="flex items-center justify-center py-12">
            <svg
              className="animate-spin h-12 w-12 text-purple-600"
              fill="none"
              viewBox="0 0 24 24"
            >
              <circle
                className="opacity-25"
                cx="12"
                cy="12"
                r="10"
                stroke="currentColor"
                strokeWidth="4"
              />
              <path
                className="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
              />
            </svg>
          </div>
        ) : (
          <>
            {/* Fields by Entity Type */}
            {Object.keys(fieldsByEntity).length === 0 ? (
              <div className="bg-white rounded-lg shadow-sm p-12 text-center">
                <svg
                  className="mx-auto h-12 w-12 text-gray-400"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2"
                  />
                </svg>
                <h3 className="mt-4 text-lg font-medium text-gray-900">No Custom Fields</h3>
                <p className="mt-2 text-gray-500">
                  Get started by creating your first custom field.
                </p>
                <button
                  onClick={() => setShowCreateModal(true)}
                  className="mt-6 px-6 py-3 bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-lg font-medium hover:from-purple-700 hover:to-pink-700 transition-all"
                >
                  Add Custom Field
                </button>
              </div>
            ) : (
              <div className="space-y-6">
                {Object.entries(fieldsByEntity).map(([entityType, entityFields]) => (
                  <div key={entityType} className="bg-white rounded-lg shadow-sm overflow-hidden">
                    <div className="px-6 py-4 border-b border-gray-200 bg-gray-50">
                      <h2 className="text-xl font-semibold text-gray-900 capitalize">
                        {entityType} Fields ({entityFields.length})
                      </h2>
                    </div>

                    <div className="overflow-x-auto">
                      <table className="w-full">
                        <thead className="bg-gray-50 border-b border-gray-200">
                          <tr>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Field Label
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Field Name
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Type
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Required
                            </th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Show in List
                            </th>
                            <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                              Actions
                            </th>
                          </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                          {entityFields
                            .sort((a, b) => a.displayOrder - b.displayOrder)
                            .map((field) => (
                              <tr key={field.id} className="hover:bg-gray-50">
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <div className="text-sm font-medium text-gray-900">
                                    {field.fieldLabel}
                                  </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <div className="text-sm text-gray-500 font-mono">
                                    {field.fieldName}
                                  </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  <span className="px-2 py-1 text-xs font-medium rounded-full bg-blue-100 text-blue-800">
                                    {field.fieldType}
                                  </span>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  {field.isRequired ? (
                                    <span className="text-red-600">Yes</span>
                                  ) : (
                                    <span className="text-gray-400">No</span>
                                  )}
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap">
                                  {field.showInList ? (
                                    <span className="text-green-600">Yes</span>
                                  ) : (
                                    <span className="text-gray-400">No</span>
                                  )}
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                                  <button
                                    onClick={() => setEditingField(field)}
                                    className="text-purple-600 hover:text-purple-900 mr-4"
                                  >
                                    Edit
                                  </button>
                                  <button
                                    onClick={() => handleDelete(field.id)}
                                    className="text-red-600 hover:text-red-900"
                                  >
                                    Delete
                                  </button>
                                </td>
                              </tr>
                            ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </>
        )}

        {/* Create/Edit Modal */}
        {(showCreateModal || editingField) && (
          <CustomFieldModal
            isOpen={true}
            onClose={() => {
              setShowCreateModal(false)
              setEditingField(null)
            }}
            onSuccess={() => {
              setShowCreateModal(false)
              setEditingField(null)
              fetchFields()
            }}
            editField={editingField}
            subAccountId={subAccountId}
          />
        )}
      </div>
    </div>
  )
}

// Custom Field Create/Edit Modal
interface CustomFieldModalProps {
  isOpen: boolean
  onClose: () => void
  onSuccess: () => void
  editField?: CustomFieldDefinition | null
  subAccountId: string
}

function CustomFieldModal({
  isOpen,
  onClose,
  onSuccess,
  editField,
  subAccountId,
}: CustomFieldModalProps) {
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [formData, setFormData] = useState({
    entityType: editField?.entityType || 'contact',
    fieldName: editField?.fieldName || '',
    fieldLabel: editField?.fieldLabel || '',
    fieldType: editField?.fieldType || 'text',
    fieldOptions: editField?.fieldOptions || [],
    isRequired: editField?.isRequired || false,
    defaultValue: editField?.defaultValue || '',
    displayOrder: editField?.displayOrder || 0,
    showInList: editField?.showInList || false,
  })
  const [optionsText, setOptionsText] = useState(
    editField?.fieldOptions?.map((o) => `${o.value}|${o.label}`).join('\n') || ''
  )

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setLoading(true)

    try {
      // Validation
      if (!formData.fieldName || !formData.fieldLabel || !formData.fieldType) {
        throw new Error('Field name, label, and type are required')
      }

      // Parse options for dropdown
      let fieldOptions: Array<{ value: string; label: string }> = []
      if (formData.fieldType === 'dropdown' && optionsText.trim()) {
        fieldOptions = optionsText
          .split('\n')
          .filter((line) => line.trim())
          .map((line) => {
            const [value, label] = line.split('|')
            return { value: value.trim(), label: label?.trim() || value.trim() }
          })
      }

      const url = editField
        ? `/api/v1/custom-fields/${editField.id}?subAccountId=${subAccountId}`
        : '/api/v1/custom-fields'

      const method = editField ? 'PUT' : 'POST'

      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...formData,
          fieldOptions,
          subAccountId,
        }),
      })

      const data = await response.json()

      if (!data.success) {
        throw new Error(data.error || 'Failed to save custom field')
      }

      // Success!
      onSuccess()
    } catch (err: any) {
      setError(err.message || 'Failed to save custom field')
    } finally {
      setLoading(false)
    }
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-lg w-full max-w-2xl max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="sticky top-0 bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between">
          <h2 className="text-2xl font-bold text-gray-900">
            {editField ? 'Edit Custom Field' : 'Add Custom Field'}
          </h2>
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {/* Error Message */}
          {error && (
            <div className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded-lg">
              {error}
            </div>
          )}

          {/* Entity Type */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Entity Type <span className="text-red-500">*</span>
            </label>
            <select
              value={formData.entityType}
              onChange={(e) => setFormData({ ...formData, entityType: e.target.value })}
              disabled={!!editField}
              required
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 disabled:opacity-50 disabled:bg-gray-50"
            >
              <option value="contact">Contact</option>
              <option value="deal">Deal</option>
              <option value="company">Company</option>
              <option value="task">Task</option>
            </select>
            {editField && (
              <p className="mt-1 text-xs text-gray-500">Entity type cannot be changed after creation</p>
            )}
          </div>

          {/* Field Name & Label */}
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Field Name <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                value={formData.fieldName}
                onChange={(e) =>
                  setFormData({
                    ...formData,
                    fieldName: e.target.value.toLowerCase().replace(/[^a-z0-9_]/g, '_'),
                  })
                }
                disabled={!!editField}
                required
                placeholder="lead_type"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 disabled:opacity-50 disabled:bg-gray-50 font-mono text-sm"
              />
              {editField && (
                <p className="mt-1 text-xs text-gray-500">Field name cannot be changed</p>
              )}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Field Label <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                value={formData.fieldLabel}
                onChange={(e) => setFormData({ ...formData, fieldLabel: e.target.value })}
                required
                placeholder="Lead Type"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
              />
            </div>
          </div>

          {/* Field Type */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Field Type <span className="text-red-500">*</span>
            </label>
            <select
              value={formData.fieldType}
              onChange={(e) => setFormData({ ...formData, fieldType: e.target.value as CustomFieldDefinition['fieldType'] })}
              required
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
            >
              <option value="text">Text</option>
              <option value="textarea">Text Area</option>
              <option value="number">Number</option>
              <option value="date">Date</option>
              <option value="dropdown">Dropdown</option>
              <option value="checkbox">Checkbox</option>
              <option value="email">Email</option>
              <option value="phone">Phone</option>
              <option value="url">URL</option>
            </select>
          </div>

          {/* Dropdown Options */}
          {formData.fieldType === 'dropdown' && (
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Dropdown Options <span className="text-red-500">*</span>
              </label>
              <textarea
                value={optionsText}
                onChange={(e) => setOptionsText(e.target.value)}
                rows={5}
                placeholder="lead|Lead&#10;team_member|Team Member&#10;vendor|Vendor"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 font-mono text-sm resize-none"
              />
              <p className="mt-1 text-xs text-gray-500">
                One option per line. Format: <code className="bg-gray-100 px-1 py-0.5 rounded">value|Label</code>
              </p>
            </div>
          )}

          {/* Settings */}
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="flex items-center gap-3 p-3 border border-gray-300 rounded-lg hover:bg-gray-50 cursor-pointer">
                <input
                  type="checkbox"
                  checked={formData.isRequired}
                  onChange={(e) => setFormData({ ...formData, isRequired: e.target.checked })}
                  className="w-5 h-5 text-purple-600 focus:ring-purple-500 border-gray-300 rounded"
                />
                <span className="text-sm font-medium text-gray-700">Required Field</span>
              </label>
            </div>

            <div>
              <label className="flex items-center gap-3 p-3 border border-gray-300 rounded-lg hover:bg-gray-50 cursor-pointer">
                <input
                  type="checkbox"
                  checked={formData.showInList}
                  onChange={(e) => setFormData({ ...formData, showInList: e.target.checked })}
                  className="w-5 h-5 text-purple-600 focus:ring-purple-500 border-gray-300 rounded"
                />
                <span className="text-sm font-medium text-gray-700">Show in List View</span>
              </label>
            </div>
          </div>

          {/* Display Order */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Display Order
            </label>
            <input
              type="number"
              value={formData.displayOrder}
              onChange={(e) => setFormData({ ...formData, displayOrder: parseInt(e.target.value) || 0 })}
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
            />
            <p className="mt-1 text-xs text-gray-500">
              Fields are displayed in ascending order (0, 1, 2, ...)
            </p>
          </div>

          {/* Action Buttons */}
          <div className="flex gap-3 pt-4 border-t border-gray-200">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-6 py-3 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 font-medium transition-colors"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={loading}
              className="flex-1 px-6 py-3 bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-lg font-medium hover:from-purple-700 hover:to-pink-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all flex items-center justify-center gap-2"
            >
              {loading ? (
                <>
                  <svg
                    className="animate-spin h-5 w-5"
                    fill="none"
                    viewBox="0 0 24 24"
                  >
                    <circle
                      className="opacity-25"
                      cx="12"
                      cy="12"
                      r="10"
                      stroke="currentColor"
                      strokeWidth="4"
                    />
                    <path
                      className="opacity-75"
                      fill="currentColor"
                      d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                    />
                  </svg>
                  Saving...
                </>
              ) : (
                <>{editField ? 'Update Field' : 'Create Field'}</>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
